.parts:nth-child(1) {
  -webkit-animation: orbit1 18s infinite;
          animation: orbit1 18s infinite;
  -webkit-animation-delay: 0.01s;
          animation-delay: 0.01s;
  background-color: #ffc000;
}

@-webkit-keyframes orbit1 {
  20% {
    opacity: 1;
  }
  30% {
    -webkit-transform: rotateZ(-253deg) rotateY(118deg) translateX(10rem)
      rotateZ(253deg);
            transform: rotateZ(-253deg) rotateY(118deg) translateX(10rem)
      rotateZ(253deg);
  }
  80% {
    -webkit-transform: rotateZ(-253deg) rotateY(118deg) translateX(10rem)
      rotateZ(253deg);
            transform: rotateZ(-253deg) rotateY(118deg) translateX(10rem)
      rotateZ(253deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: rotateZ(-253deg) rotateY(118deg) translateX(15rem)
      rotateZ(253deg);
            transform: rotateZ(-253deg) rotateY(118deg) translateX(15rem)
      rotateZ(253deg);
  }
}

@keyframes orbit1 {
  20% {
    opacity: 1;
