i {
  margin-right: 0.5rem;
}

@-webkit-keyframes rotate {
  100% {
    -webkit-transform: rotateY(360deg) rotateX(360deg);
            transform: rotateY(360deg) rotateX(360deg);
  }
}

@keyframes rotate {
  100% {
    -webkit-transform: rotateY(360deg) rotateX(360deg);
            transform: rotateY(360deg) rotateX(360deg);
  }
}

.particles {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  transform-style: preserve-3d;
  perspective: 1000px;
  -webkit-animation: rotate 18s infinite linear;
          animation: rotate 18s infinite linear;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-perspective: 1000px;
  perspective: 1000px;
  -webkit-animation: rotate 14s infinite linear;
  animation: rotate 14s infinite linear;
}

.parts {
  position: absolute;
  width: 0.1rem;
  height: 0.1rem;
  border-radius: 50%;
  opacity: 0;
}

input.reverse {
  background: #001;
  color: #fff;
}

textarea.reverse {
  background: #001;
  color: #fff;
}

.company-info-reverse {
  color: #001;
  text-shadow: none;
}

/* MEADIA QUERIES */

@media screen and (min-width: 1172px) {
  .projects {
    grid-template-columns: repeat(4, 1fr);
  }
  .highlightedProject {
    grid-template-columns: repeat(3, 1fr);
  }
  #home {
    text-align: center;
  }
}

@media screen and (max-width: 1171px) {
  .projects {
    grid-template-columns: repeat(2, 1fr);
  }
  .highlightedProject {
    grid-template-columns: repeat(2, 1fr);
  }
  .highlightedList {
    margin: auto;
    grid-row: 1 / -1;
    grid-column: 2 / 3;
    padding: 0;
  }
  .item-description {
    grid-column: 1 / 3;
  }
  .highlightedList__icons {
    display: flex;
  }
  .highlightedList__icons li a {
    display: flex;
    margin-top: 0;
    margin-right: 0.5em;
  }
  #home {
    text-align: center;
  }
}

/* iPhone 11 Pro and below */
@media screen and (max-width: 824px) {
  .highlightedProject {
    grid-template-columns: repeat(2, 1fr);
  }
  .about-info .bio-image {
    margin: auto;
  }
  .openImageModal {
    height: 333px;
  }
  main {
    align-items: center;
    text-align: center;
  }
  main .lg-heading,
  main .dzenis {
    line-height: 1;
    margin-bottom: 1rem;
    font-size: 4.4rem;
  }
  ul.menu-nav,
  div.menu-nav,
  div.menu-branding {
    float: none;
    width: 100%;
    min-height: 0;
  }
  ul.menu-nav.show,
  div.menu-branding.show {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  .menu-nav {
    /* height: 100vh; */
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
    font-size: 24px;
  }
  .menu-branding {
    /* height: 25vh; */
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
  .menu-branding .portrait {
    background: url(/static/media/logo.eb1aa3af.webp);
    width: 150px;
    height: 150px;
  }

  /* If WebP is supported  */
  .webp .portrait {
    background: url(/static/media/logo.eb1aa3af.webp);
  }

  .no-webp .portrait {
    background: url(/static/media/logo.b211e9ac.png);
  }

  .about-info {
    grid-template-areas:
      "bioimage"
      "bio"
      "job1"
      "job2"
      "job3";
    grid-template-columns: 1fr;
  }
  .projects {
    grid-template-columns: repeat(2, 1fr);
  }
  nav a {
    padding: 2rem;
  }
  ul li {
    text-align: left;
  }
  .job ul li {
    /* justify-items: center; */
    display: inherit;
  }
}

.header-note {
  text-align: center;
  letter-spacing: 4px;
  margin-top: 0.1rem;
  margin-bottom: 1rem;
  font-size: 0.85rem;
}

@media screen and (max-width: 640px) {
  .highlightedProject {
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 824px) and (orientation: landscape) {
  .modal-video-close-btn {
    display: none;
  }
  .highlightedList li {
    font-size: 0.9em;
  }
  .menu .nav-link {
    font-size: 1.4rem;
    padding: 0.2rem;
    margin: auto;
  }
  .about-info .bio-image {
    margin: auto;
  }
  .job ul li {
    justify-items: center;
    display: grid;
  }
  .openImageModal {
    height: 333px;
  }
}

@media screen and (max-width: 612px) {
  main#home {
    background-size: auto 100%;
    background-position: left;
    background-repeat: no-repeat;
  }
  main {
    padding: 2rem;
  }
  main #home h1 {
    margin-top: 100vh;
  }
  main > h2 {
    font-size: 1rem;
  }
  main .lg-heading,
  main .dzenis {
    font-size: 4rem;
    margin-top: 2.65rem;
  }
  main .icons a {
    padding: 0.85rem;
  }
  main .icons a.cv {
    margin-left: 0;
  }
  ul li {
    text-align: left;
  }
  .contact-plus {
    font-size: 0.88em;
  }
  .menu .nav-link {
    font-size: 1.4rem;
  }
  .projects {
    grid-template-columns: 1fr;
  }
  .highlightedProject {
    grid-template-columns: 1fr;
  }
  .particles {
    padding-top: 4rem;
  }
  .dox {
    display: none;
  }
  .highlightedList {
    padding: inherit;
  }
  nav a {
    padding: 0.7rem;
  }
  .about-info .bio-image {
    margin: auto;
  }
  .header-note {
    font-size: 0.77rem;
    letter-spacing: normal;
  }
  /* .menu-btn {
    top: 1rem;
  } */
}

@media screen and (max-width: 767px) and (orientation: portrait) {
  .firstStack li i,
  .secondStack li i,
  .thirdStack li i {
    display: block;
    font-size: 0.85rem;
  }
  .thirdStack li span.diff-text {
    display: inline-block;
  }
}

@media screen and (max-width: 1136px) {
